import React from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTwitter, faTwitch, faYoutube } from '@fortawesome/free-brands-svg-icons'

class SocialMedia extends React.Component {
    render() {
        return (
            <div id="socialMedia">
                <a className="text-light px-2" href="https://twitter.com/rorazoro">
                    <FontAwesomeIcon icon={faTwitter} />
                </a>
                <a className="text-light px-2" href="https://www.twitch.tv/rorazoro">
                    <FontAwesomeIcon icon={faTwitch} />
                </a>
                <a className="text-light px-2" href="https://www.youtube.com/channel/UCWxCY3aE4P7vSjelY5nRQOg">
                    <FontAwesomeIcon icon={faYoutube} />
                </a>
            </div>
        )
    }
}

export default SocialMedia