import React from "react"

import { Link } from "gatsby"
import SocialMedia from "./socialMedia"

class Footer extends React.Component {
    render() {
        const copyrightYear = new Date().getFullYear()

        return (
            <div className="container-fluid bg-dark">
                <footer className="container text-light text-center">
                    <div className="mb-5">
                        <h3>Questions or interested in working together?</h3>
                        <Link className="btn btn-primary mt-2" to="/contact">Get in touch!</Link>
                    </div>
                    <div>
                        <h4><SocialMedia /></h4>
                    </div>
                    <Link className="text-light" to="/"><em>&#169; {copyrightYear} Sam Chancey</em></Link>
                </footer>
            </div>
        )
    }
}

export default Footer